import React, { useState, useEffect } from 'react';
import images from '../../constants/images';
import { HiMenuAlt4, HiX } from 'react-icons/hi';
import { motion } from 'framer-motion';
import {Link} from 'react-router-dom'
import { AiOutlineLinkedin, AiOutlineInstagram, AiOutlineYoutube } from "react-icons/ai";
import './Navbar.css';

const Navbar = () => {
  const [toggle, setToggle] = useState(false);
  const [navbar, setNavbar] = useState(false);
  const changeBackground = () => {
    console.log(window.scrollY)
    if (window.scrollY >= 50) {
      setNavbar(true)
    } else {
      setNavbar(false)
    }
  }
  useEffect(() => {
    changeBackground()
    // adding the event when scroll change background
    window.addEventListener("scroll", changeBackground)
  })



  return (
    <nav className={navbar ? 'app__navbar-active'  : 'app__navbar'}>
      <div className = {navbar ? 'app__logo-small'  : 'app__logo'} >
        <img src={navbar ? images.acmlogo  : images.acmlogo}  />
      </div>
      <ul className='app__navbar-links'>
        <li className="p__opensans navlink"><Link to='/bilkentacm/'>Home</Link></li>
        <li className="p__opensans navlink"><Link to='/AboutUs'>About Us</Link></li>
        <li className="p__opensans navlink"><Link to='/Events'>Events</Link></li>
        <li className="p__opensans navlink"><Link to='/Team'>Team</Link></li>

      </ul>

      <div className='app__navbar-menu'>
        <HiMenuAlt4 className='menubuttons' onClick={() => setToggle(true)} />

        {toggle && (
          <motion.div
            whileInView={{ x: [300,0] }}
            initial={{ opacity: 0}}
            animate={{ opacity: 1 }}

            // transition={{ duration: 0.5, ease: 'easeOut' }}
          >

            <HiX className='menubuttons' onClick={() => setToggle(false)}  />
       

            <ul className='app__navbar-hlinks'>
              <li className="p__opensans"><Link to='/bilkentacm/' onClick={() => setToggle(false)}>Home</Link></li>
              <li className="p__opensans"><Link to='/AboutUs' onClick={() => setToggle(false)}>About Us</Link></li>
              <li className="p__opensans"><Link to='/Events'onClick={() => setToggle(false)}>Events</Link></li>
              <li className="p__opensans"><Link to='/Team' onClick={() => setToggle(false)}>Team</Link></li>

            </ul>

          </motion.div>
        )}

        
      </div>

      <div className={navbar ? 'app__socials-active'  : 'app__socials'}>
          <a href='https://tr.linkedin.com/company/acmbilkent' target="_blank" className='app__socials-link'><AiOutlineLinkedin /></a>
          <a href='https://www.instagram.com/bilkentacm/'target="_blank" className='app__socials-link'><AiOutlineInstagram /></a>
          <a href='https://www.youtube.com/channel/UCp-qhh-NPerf5DzWmiJyxRw/null' target="_blank" className='app__socials-link'><AiOutlineYoutube /></a>

        </div>

    </nav>
  )
}

export default Navbar;

